@import './default/breakpoints';
$uniqloReg: "Uniqlo Pro Regular", "uniqloRegregular", "Helvetica Neue",
  Helvatica, Arial;

$uniqloBold: "Uniqlo Pro Bold", "$uniqloRegbold", "Helvetica Neue", Helvatica,
  Arial;

$heleveticaNeue: "Helvetica Neue", Helvatica, Arial;

//Tokens
:root {
    --helvetica: #{$heleveticaNeue};
    --uniqloProReg: #{$uniqloReg};
    --uniqloProBold: #{$uniqloBold};
}



// Plugins



// Default
@import './default/reset';
@import './default/colors';
@import './default/typography';

// Components 
@import './components/atoms/buttons';
@import './components/atoms/Hero';
@import './components/atoms/MenuItem';
@import './components/atoms/Video';
@import './components/atoms/AboutDescription';
@import './components/atoms/DividerHeading';
@import './components/atoms/Function';
@import './components/Quotes';
@import './components/atoms/InfoCard';
@import './components/atoms/Tabs';





@import './components/molecules/products';
@import './components/molecules/MenuContainer';

.showMB{
  display: block;
  @include bp(sm){
    display: none;

  }
}
.showTB{
  display: none;
  @include bp(sm){
    display: block;
  }
  @include bp(mid){
    display: none;
  }
}


.showPC{
  display: none;
  @include bp(mid){
    display: block;
  }
}